import React, { useEffect, useState } from 'react';
import Editor from '../Editor';  
import Configuration from "../Configuration";
import SurfSerial from '../../utils/serial';
import { getInfo, compile } from '../../utils/clientApi';
import './surfboard.scss';

const serial = new SurfSerial();

const Surfboard = props => {
    const { appId } = props;

    const [selectBoard, setSelectBoard] = useState({});
    const [boards, setBoards] = useState([]);
    const [libs, setLibs] = useState([]);
    const [files, setFiles] = useState({});
    const [connectState, setConnectState] = useState(false);
    const [isFlashed, setIsFlashed] = useState(false);
    const [termText, setTermText] = useState("");
    const [hex, setHex] = useState("");

    useEffect(() => {
        let isSubscribed = true;

        const handleInfo = async () => {
            const info = await getInfo(appId);

            if (isSubscribed) {
                setFiles(info.files);
                setLibs(info.libs);
                setBoards(info.compatibleBoards)
            }
        };

        handleInfo();

        return () => isSubscribed = false;

    }, [appId]);

    const handleConnect = e => {
        e.preventDefault()
        const baud = selectBoard.baud;
        serial.setBaud(baud);
        serial.setupSerial();
        if (connectState === false) {
            serial.openSerial().then(
                value => {
                    setConnectState(value);
                    serial.serial.on('data', function(data) {
                        // TODO change: test shouldn't be in the terminal. It doesn't exist yet!
                        setTermText(data.toString().replace(/\r\n/g, '\n').replace(/\n/g, '\r\n'));
                    });
                }
                // can also add handleReject value function like the above
            );
        } else {
            serial.closeSerial();//close port
        }
    }
    
    const handleFlash = e => {
        
        setIsFlashed(true);
    };

    const handleCompile = async e => {
        const selectedBoard = e.target.value;
        setSelectBoard(selectedBoard);
        
        const compileInfo = {
            board: selectedBoard.fqbn,
            files: files,
            flags: {
                verbose: true,
                preferLocal: false
            },
            libs: libs
        }

        const result = await compile(compileInfo);
        
        setHex(result.hex);


        
    }

    return (
        /*TODO include app loader dimensions */
        <div className="surfboard-container" id='surfboard-container' style={{height:600, width:500}}>
            {/*TODO include loading state */}
            <Editor code={files.content} pxHeight={400} />
            <Configuration boards={boards} />
        </div>
    )
}

export default Surfboard;