import React from 'react';
import { ButtonGroup, SIZE, SHAPE } from 'baseui/button-group';
import { Button } from 'baseui/button';


const Configuration = props => {
    const { boards } = props;

    const buttons = () => {
        const buttons = [];
        
        for (const board of boards) {
            buttons.push(<Button>{board.name}</Button>);
        }

        return buttons;
    }
    
    return (
        <div className="configuration" id="surfboard-configuration">
            <ButtonGroup size={SIZE.compact} shape={SHAPE.pill}>
                {buttons}
            </ButtonGroup>
        </div>
    )
}

export default Configuration;